'use strict';

var _require = require('@snakesilk/megaman-traits'),
    Door = _require.Door;

function factory(parser, node) {
    var directionNode = node.getElementsByTagName('direction')[0];
    var direction = directionNode ? parser.getVector2(directionNode) : undefined;

    var oneWay = parser.getBool(node, 'one-way');

    return function createDoor() {
        var trait = new Door();
        if (direction) {
            trait.direction.copy(direction);
        }
        trait.oneWay = oneWay;
        return trait;
    };
}

module.exports = factory;