'use strict';

var _require = require('@snakesilk/megaman-traits'),
    Elevator = _require.Elevator;

function factory(parser, node) {
    var nodes = [];
    var speed = 10;

    var pathNode = node.getElementsByTagName('path')[0];
    if (pathNode) {
        speed = parser.getFloat(pathNode, 'speed');
        var nodeNodes = pathNode.getElementsByTagName('node');
        if (nodeNodes) {
            for (var nodeNode, i = 0; nodeNode = nodeNodes[i++];) {
                var _node = parser.getVector2(nodeNode);
                nodes.push(_node);
            }
        }
    }

    return function createElevator() {
        var trait = new Elevator();
        trait.speed = speed;
        nodes.forEach(function (node) {
            trait.addNode(node);
        });
        return trait;
    };
}

module.exports = factory;